body {
  font-family: 'Open Sans', sans-serif;
  background-color: #fff;
}

header {
  color: #fff;
  background-color: #103D5D;
  padding: 33px 0 25px 15px;
}

header p {
  width: 400px;
  margin: 0 auto;
}
header .wrapper {
  display: flex;
  justify-content: space-between;
}

header h1 {
  font-size: 25px;
  font-weight: 600;
  letter-spacing: 0.3px;
  display: inline-block;
  margin: 0;
  vertical-align: middle;
  
}

.fa {
  font-size: 22px;
  transform: translateY(50deg);  
}

header i {
  vertical-align: middle;
  margin-left: 12px;
}

.container {
  display: flex;
  max-width: 1100px;
  margin: 0 auto;
  padding-top: 30px;
}

.wrapper {
  max-width: 1100px;
  margin: 0 auto;
}

.col-md-7{
  background-color: whitesmoke;
}
li{
  text-decoration-line: none;
text-decoration-style: none;
list-style-type: none;
padding: 5px;
}
.row{
  text-align: center;
}
.img-fluid{
  height: 100px;
  padding: 5px;
}
.user-profile{
  text-align: center;
}
.form{
  width: 100%;
}
.col{
  margin: 0 auto;
}
ul{
  background-color: #2F2F2F;
  padding: 5px;
}
p{
  padding: 2px;
padding-right: 2px;
}
form{
  padding-bottom: 5px;
}